
import { defineComponent } from 'vue'
import { USER_ROLE_LIST, APM204001sc02_List, APM204001sc02_Req_Body, APM204001sc02_RES_Detail, APM204001sc02_REQ_Update, PERSON_LIST } from '@/shared/type/apm204001sc02.type';
import { CommonCodeType } from '@/shared/type/commonCodeType';
import { modalController } from '@ionic/vue';
import { FormValidation } from '@/shared/services/FormValidationService';
import CurrencyInput from '@/shared/components/CurrencyInput/CurrencyInput.vue';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { screenMode } from '@/shared/type/Approval.type';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { DataCenter } from '@/shared/services/DataCenter';

const formValidation = new FormValidation();

export default defineComponent({
    name: "apm204001sc02",
    components: {
        CurrencyInput,
        ModifyCard,
        BreadCrumb
    },
    data() {
        const modifyCard = {
            registerTellerId: '',
            registerTellerName: '',
            registerDate: '',
            registerTime: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: '',
        }
        const companyAuthorizationLineSettingInfoDetail = {
            customerNo: "",
            masterUserID: "",
            authorizationAuthorityTypeCode: "",
            authorizationBizTypeCode1: "",
            authorizationBizTypeCode2: "",
            authorizationCodeName: "",
            authorizationLineAssignTypeCode: "",
            firstRegisterDate: "",
            firstRegisterTellerID: "",
            firstRegisterTellerName: "",
            firstRegisterTime: "",
            lastChangeDate: "",
            lastChangeTellerID: "",
            lastChangeTellerName: "",
            lastChangeTime: "",
            requiredAmoungRangeYN: "",
            subscriptionApprovalTypeCode: "",
            mainUserRoleID: "",
            authStepList: [] as APM204001sc02_List[],
        } as APM204001sc02_RES_Detail;
        return {
            companyAuthorizationLineSettingInfoDetail,
            requiredAmoungRangeYN: [] as CommonCodeType[],
            userRoleList: [] as USER_ROLE_LIST[],
            isEdit: true as boolean,
            isRequired: false as boolean,
            isInvalidAmount: false as boolean,
            isRequiredStepField: false as boolean,
            isInvalidAmountInList: false as boolean,
            isRequiredStepFieldList: false as boolean,
            authorizationStep: {
                seqNo: 0,
                approverID: "",
                maxAmount: 0,
                minAmount: 0,
                authorizationRoleCode: "",
                currencyCode: "USD",
                approverName: "",
                userRoleName: "",
                personList: [] as PERSON_LIST[],
            } as APM204001sc02_List,
            modifyCard,
            authorizationCode: '' as any,
            isInvalidMaxAmount: false,
            query: { customerNo: this.$route.query.customerNo, masterUserID: this.$route.query.masterUserID },
            invalidFinal: false,
            requireField: false,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
            previousAuthStep: [] as any
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            this.requestAuthorizationSettingDetail()
                .then(() => this.requestUserRoleList())
        } else {
            await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                const authStepList = authDetailParsed.authStepList.map((item: APM204001sc02_List) => {
                    if (item.authorizationRoleCode) {
                        item.personList = this.handleRequestPersonList("stepList", item.authorizationRoleCode, authDetailParsed.customerNo);
                    }
                });
                console.log('authStepList',authStepList);
                console.log("authDetailParsed",authDetailParsed);
                this.companyAuthorizationLineSettingInfoDetail = { ...authDetailParsed, ...authStepList };
                this.modifyCard = {
                    registerTellerId: this.companyAuthorizationLineSettingInfoDetail.firstRegisterTellerID,
                    registerTellerName: this.companyAuthorizationLineSettingInfoDetail.firstRegisterTellerName,
                    registerDate: this.companyAuthorizationLineSettingInfoDetail.firstRegisterDate,
                    registerTime: this.companyAuthorizationLineSettingInfoDetail.firstRegisterTime,
                    updatedTellerId: this.companyAuthorizationLineSettingInfoDetail.lastChangeTellerID,
                    updatedTellerName: this.companyAuthorizationLineSettingInfoDetail.lastChangeTellerName,
                    updatedDate: this.companyAuthorizationLineSettingInfoDetail.lastChangeDate,
                    updatedTime: this.companyAuthorizationLineSettingInfoDetail.lastChangeTime,
                }
                const finalIndex: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.findIndex(item => item.approverID == this.$route.query.masterUserID || item.authorizationRoleCode == 'FINAL');
                if (finalIndex !== -1) {
                    const finalObject: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.splice(finalIndex, 1)[0];
                    this.companyAuthorizationLineSettingInfoDetail.authStepList.push(finalObject);
                }
            });


        }

    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        authorizationLineOption(): string {
            return this.companyAuthorizationLineSettingInfoDetail.subscriptionApprovalTypeCode
                ? `${this.companyAuthorizationLineSettingInfoDetail.subscriptionApprovalTypeCode}:${this.$codeConverter.convertValue('SubscriptionApprovalTypeCode', this.companyAuthorizationLineSettingInfoDetail.subscriptionApprovalTypeCode)}`
                : "";
        },
        authorizationLineRolePersonOptionCode(): string {
            return this.companyAuthorizationLineSettingInfoDetail.authorizationLineAssignTypeCode
                ? `${this.companyAuthorizationLineSettingInfoDetail.authorizationLineAssignTypeCode}:${this.$codeConverter.convertValue('AuthorizationLineAssignTypeCode', this.companyAuthorizationLineSettingInfoDetail.authorizationLineAssignTypeCode)}`
                : "";
        },
        isValidationRole(): boolean {
            return this.isRequiredStepField && !this.authorizationStep.authorizationRoleCode ? true : false;
        },
        isValidationPerson(): boolean {
            return this.isRequiredStepField && !this.authorizationStep.approverID ? true : false;
        },
        isDiabledSelectPerson(): boolean {
            return this.isEdit || !this.authorizationStep.authorizationRoleCode ? true : false;
        },
        isUseAmountRange(): boolean {
            return this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN == "Y" && !this.isEdit ? false : true;
        },
        mappingAuthorizationCode(): any {
            this.$store.state.CommonCodeModule.commonCode.BusinessCategoryCode.find((item: any) => {
                if (item.key == this.companyAuthorizationLineSettingInfoDetail.authorizationBizTypeCode1) {
                    this.authorizationCode = item.value;
                    this.companyAuthorizationLineSettingInfoDetail.authorizationBizTypeCode1 = this.authorizationCode;
                }
            })
            return this.companyAuthorizationLineSettingInfoDetail
        },
        getFinalAuthorizerID(): any {
            let FinalAuthorizerItem: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.find((items: any) => items.approverID == this.$route.query.masterUserID || items.authorizationRoleCode == 'FINAL')
            return FinalAuthorizerItem ? `${FinalAuthorizerItem.authorizationRoleCode}:${FinalAuthorizerItem.userRoleName}` : ''
        },
        getFinalAuthorizerUserName(): any {
            let FinalAuthorizerItem: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.find((items: any) => items.approverID == this.$route.query.masterUserID);
            let AuthorizerUserName: any;
            if (FinalAuthorizerItem) {
                if (FinalAuthorizerItem.userName == '') {
                    AuthorizerUserName = FinalAuthorizerItem.customerName
                } else {
                    AuthorizerUserName = FinalAuthorizerItem.userName
                }
                if (FinalAuthorizerItem.authorizationRoleCode == 'FINAL') {
                    if (Array.isArray(FinalAuthorizerItem.personList)) {
                        FinalAuthorizerItem.personList.forEach((item: any) => {
                            AuthorizerUserName = item.userName.replace(/\s/g, '') ? item.userName : item.customerName
                        });
                    }
                }
            }
            return AuthorizerUserName;
        },
        isCurrentMaxAmount(): any {
            if (this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN == 'Y') {
                const newCompanyAuthorizationLineSettingInfoDetail = [] as any;
                this.companyAuthorizationLineSettingInfoDetail.authStepList.map((item) => {
                    if (item.authorizationRoleCode != 'FINAL' && item.authorizationRoleCode != 'STP') {
                        newCompanyAuthorizationLineSettingInfoDetail.push(item)
                    }
                });
                return newCompanyAuthorizationLineSettingInfoDetail.find((item) => {
                    return (
                        Number(item.maxAmount) >= Number(this.authorizationStep.maxAmount) &&
                        this.authorizationStep.authorizationRoleCode !== 'FINAL' &&
                        this.authorizationStep.authorizationRoleCode !== 'STP' &&
                        this.authorizationStep.authorizationRoleCode !== 'CHECK'
                    );
                });
            } else {
                return null;
            }

        },
        isNotExistingFinalAuthorizer(): any {
            return this.companyAuthorizationLineSettingInfoDetail.authStepList.every((item: any) => {
                if (item.approverID != this.$route.query.masterUserID && item.authorizationRoleCode != 'FINAL') {
                    return true
                }
            })
        },
        isAssignRoleOnly(): any {
            if (this.companyAuthorizationLineSettingInfoDetail.authorizationLineAssignTypeCode == '01') {
                return true
            } else {
                return false
            }

        }
    },

    watch: {
        'authorizationStep.authorizationRoleCode'(newVal: string, oldVal: string) {
            if (newVal !== oldVal) {
                this.handleRequestPersonList("stepObj", newVal, "");
            }
        },
        'companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN'(newVal) {
            if (newVal == 'N') {
                this.requireField = false;
                this.isInvalidAmountInList = false;
            }
        }
    },

    methods: {
        async requestAuthorizationSettingDetail() {
            const body: APM204001sc02_Req_Body = {
                authorizationAuthorityTypeCode: `${this.$route.query.authorizationAuthorityTypeCode}`,
                authorizationBizTypeCode1: `${this.$route.query.authorizationBizTypeCode1}`,
                authorizationBizTypeCode2: `${this.$route.query.authorizationBizTypeCode2}`,
                customerNo: `${this.$route.query.customerNo}`,
                masterUserID: `${this.$route.query.masterUserID}`,
            }
            await this.$request.getRequest('APM20400113', body, false)
                .then(async (res: any) => {
                    if (res.header.result) {
                        const authStepList = res.body.authStepList.map((item: APM204001sc02_List) => {
                            if (item.authorizationRoleCode) {
                                item.personList = this.handleRequestPersonList("stepList", item.authorizationRoleCode, "");
                            }
                        });
                        this.companyAuthorizationLineSettingInfoDetail = { ...res.body, ...authStepList };
                        this.modifyCard = {
                            registerTellerId: this.companyAuthorizationLineSettingInfoDetail.firstRegisterTellerID,
                            registerTellerName: this.companyAuthorizationLineSettingInfoDetail.firstRegisterTellerName,
                            registerDate: this.companyAuthorizationLineSettingInfoDetail.firstRegisterDate,
                            registerTime: this.companyAuthorizationLineSettingInfoDetail.firstRegisterTime,
                            updatedTellerId: this.companyAuthorizationLineSettingInfoDetail.lastChangeTellerID,
                            updatedTellerName: this.companyAuthorizationLineSettingInfoDetail.lastChangeTellerName,
                            updatedDate: this.companyAuthorizationLineSettingInfoDetail.lastChangeDate,
                            updatedTime: this.companyAuthorizationLineSettingInfoDetail.lastChangeTime,
                        }
                        const finalIndex: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.findIndex(item => item.approverID == this.$route.query.masterUserID || item.authorizationRoleCode == 'FINAL');
                        if (finalIndex !== -1) {
                            const finalObject: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.splice(finalIndex, 1)[0];
                            this.companyAuthorizationLineSettingInfoDetail.authStepList.push(finalObject);
                        }
                    }
                    DataCenter.setData("COMPANY_AUTH_STEP", this.companyAuthorizationLineSettingInfoDetail.authStepList);
                    console.log(this.companyAuthorizationLineSettingInfoDetail.authStepList);
                    if (this.approvalType != '') {
                        this.previousAuthStep = await DataCenter.getData('COMPANY_AUTH_STEP');
                    }

                })
            this.mappingAuthorizationCode;
        },

        async requestUserRoleList() {
            const res: any = await this.$request.getRequest('COM21200113', {}, false);
            if (res.header.result) {
                res.body.list.filter((item: USER_ROLE_LIST) => {
                    if (item.userRoleID == "FINAL" || item.userRoleID == "STP" || item.userRoleID == "AUTH" || item.userRoleID == "CHECK") {
                        let isExistingFinalAuthorizer: any;
                        isExistingFinalAuthorizer = this.companyAuthorizationLineSettingInfoDetail.authStepList.find((list: any) => list.approverID == this.$route.query.masterUserID);
                        if (isExistingFinalAuthorizer) {
                            if (item.userRoleID !== this.companyAuthorizationLineSettingInfoDetail.mainUserRoleID && item.userRoleID != "FINAL" && item.userRoleID != "STP") {
                                this.userRoleList.push(item);
                            }
                        } else {
                            if (this.companyAuthorizationLineSettingInfoDetail.subscriptionApprovalTypeCode == "02") {
                                if (item.userRoleID == "STP") {
                                    this.userRoleList.push(item);
                                }
                            }
                            if (this.companyAuthorizationLineSettingInfoDetail.subscriptionApprovalTypeCode == "01") {
                                if (item.userRoleID != "STP") {
                                    this.userRoleList.push(item);
                                }
                            }
                        }
                    }

                });
            }
        },

        async handleRequestPersonList(key: "stepObj" | "stepList", userRoleID?: string, customerNo?: string) {
            switch (key) {
                case 'stepList':
                    await this.$request.getRequest('COM21200114', { userRoleID: userRoleID, customerNo: this.approvalType === '' ? this.$route.query.customerNo : customerNo }, false)
                        .then((res: any) => {
                            this.companyAuthorizationLineSettingInfoDetail.authStepList.map(
                                async (item: APM204001sc02_List) => {
                                    res.body.list.forEach((i: any) => {
                                        if (item.approverID === i.userID) {
                                            item.approverName = i.userName.replace(/\s/g, '') ? i.userName : i.customerName
                                        }
                                    })
                                    return item;
                                })
                        })
                    break;
                case 'stepObj':
                    if (userRoleID !== "")
                        await this.$request.getRequest('COM21200114', { userRoleID: userRoleID, customerNo: this.$route.query.customerNo, userID: userRoleID == "FINAL" ? this.$route.query.masterUserID : '' }, false)
                            .then((res: any) => {
                                if (res.header.result) {
                                    this.authorizationStep.personList = res.body.list;
                                    this.userRoleList.find((item: any) => {
                                        if (item.userRoleID == this.authorizationStep.authorizationRoleCode) {
                                            this.authorizationStep.userRoleName = item.userRoleName
                                        }
                                    })

                                    this.userRoleList.map((item: USER_ROLE_LIST) => {
                                        if (this.authorizationStep.approverID === item.userRoleID) {
                                            this.authorizationStep.userRoleName = item.userRoleName;
                                        }
                                    });
                                }
                            });
                    this.onResetValueWhenUserChangingAction("renderRoleList", false);
                    break;
            }
        },

        handleReorder(event: CustomEvent) {
            this.companyAuthorizationLineSettingInfoDetail.authStepList = event.detail.complete(
                this.companyAuthorizationLineSettingInfoDetail.authStepList
            );
            this.onResetValueWhenUserChangingAction("reOrder", false);
        },

        handleAddAuthorizatinoStep() {
            this.onResetValueWhenUserChangingAction("addStep", false);
            let isDuplicated = false;
            this.companyAuthorizationLineSettingInfoDetail.authStepList.forEach((item: APM204001sc02_List) => {
                if (
                    this.authorizationStep.authorizationRoleCode === item.authorizationRoleCode
                    && this.authorizationStep.approverID === item.approverID
                ) {
                    isDuplicated = true;
                }
            });
            if (isDuplicated) {
                this.$dialog.onOpen({
                    dialogMode: 'Warning',
                    content: this.$t('message.can_not_add_duplicated_data'),
                    callback: () => modalController.dismiss(),
                });
            } else if (this.companyAuthorizationLineSettingInfoDetail.authStepList.length >= 5) {
                this.$dialog.onOpen({
                    dialogMode: 'Warning',
                    content: this.$t("message.step_must_less_than_or_equal_five"),
                    callback: () => modalController.dismiss(),
                })
            } else if (this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN == "Y" && !formValidation.minMaxValueValidation(Number(this.authorizationStep.minAmount), Number(this.authorizationStep.maxAmount))) {
                this.isInvalidAmount = true;
                this.isRequiredStepField = true;
            } else {
                if (this.companyAuthorizationLineSettingInfoDetail.authorizationLineAssignTypeCode == '02') {
                    if (!this.authorizationStep.authorizationRoleCode || !this.authorizationStep.approverID) {
                        this.isRequired = false;
                        this.isRequiredStepField = true;
                        this.isRequiredStepFieldList = false;
                    } else {
                        if (this.isCurrentMaxAmount) {
                            this.isInvalidMaxAmount = true;
                            this.$dialog.onOpen({
                                dialogMode: 'Warning',
                                content: 'Current step max amount must be greater than previous step.',
                                callback: () => modalController.dismiss(),
                            })
                        } else {
                            this.companyAuthorizationLineSettingInfoDetail.authStepList.push(JSON.parse(JSON.stringify(this.authorizationStep)));
                            this.isRequiredStepField = false;
                            this.authorizationStep.maxAmount = 0;
                            this.authorizationStep.minAmount = 0;
                            this.authorizationStep.approverID = "";
                            this.authorizationStep.authorizationRoleCode = "";
                            this.isInvalidMaxAmount = false;
                        }
                    }
                } else {
                    if (!this.authorizationStep.authorizationRoleCode) {
                        this.isRequired = false;
                        this.isRequiredStepField = true;
                        this.isRequiredStepFieldList = false;
                    } else {
                        if (this.isCurrentMaxAmount) {
                            this.isInvalidMaxAmount = true;
                            this.$dialog.onOpen({
                                dialogMode: 'Warning',
                                content: 'Current step max amount must be greater than previous step.',
                                callback: () => modalController.dismiss(),
                            })
                        } else {
                            this.companyAuthorizationLineSettingInfoDetail.authStepList.push(JSON.parse(JSON.stringify(this.authorizationStep)));
                            this.isRequiredStepField = false;
                            this.authorizationStep.maxAmount = 0;
                            this.authorizationStep.minAmount = 0;
                            this.authorizationStep.approverID = "";
                            this.authorizationStep.authorizationRoleCode = "";
                            this.isInvalidMaxAmount = false;
                        }
                    }
                }


            }
            const finalIndex: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.findIndex(item => item.approverID == this.$route.query.masterUserID || item.authorizationRoleCode == 'FINAL');
            if (finalIndex !== -1) {
                const finalObject: any = this.companyAuthorizationLineSettingInfoDetail.authStepList.splice(finalIndex, 1)[0];
                this.companyAuthorizationLineSettingInfoDetail.authStepList.push(finalObject);
            }
        },

        handleDeleteAuthorizationStep(index) {
            this.onResetValueWhenUserChangingAction("deletStep", false);
            this.$dialog.onOpen({
                dialogMode: 'Question',
                content: this.$t("message.are_you_sure_you_want_to_delete"),
                callback: async () => {
                    modalController.dismiss();
                    this.companyAuthorizationLineSettingInfoDetail.authStepList = this.companyAuthorizationLineSettingInfoDetail.authStepList
                        .filter((item, i) => i !== index);
                }
            })
        },

        async requestAuthorizationCodetEdit() {
            let authorizationBizTypeCode1: any;
            this.$store.state.CommonCodeModule.commonCode.BusinessCategoryCode.find((item: any) => {
                if (item.value == this.companyAuthorizationLineSettingInfoDetail.authorizationBizTypeCode1) {
                    authorizationBizTypeCode1 = item.key
                }
            })
            const body: APM204001sc02_REQ_Update = {
                customerNo: this.companyAuthorizationLineSettingInfoDetail.customerNo,
                masterUserID: this.companyAuthorizationLineSettingInfoDetail.masterUserID,
                requiredAmoungRangeYN: this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN,
                authorizationBizTypeCode1: authorizationBizTypeCode1,
                authorizationBizTypeCode2: this.companyAuthorizationLineSettingInfoDetail.authorizationBizTypeCode2,
                subscriptionApprovalTypeCode: this.companyAuthorizationLineSettingInfoDetail.subscriptionApprovalTypeCode,
                authorizationAuthorityTypeCode: this.companyAuthorizationLineSettingInfoDetail.authorizationAuthorityTypeCode,
                authorizationLineAssignTypeCode: this.companyAuthorizationLineSettingInfoDetail.authorizationLineAssignTypeCode,
                authStepList: this.companyAuthorizationLineSettingInfoDetail.authStepList.map(
                    (item: APM204001sc02_List, index: number) => {
                        item.seqNo = index + 1;
                        item.maxAmount = Number(item.maxAmount);
                        item.minAmount = Number(item.minAmount);
                        delete item.personList; // Server not require this list
                        return item;
                    }),
            }
            await this.$request.getRequest('APM20400131', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.isEdit = true;
                        this.isRequired = false;
                        this.isRequiredStepFieldList = false;
                        this.isInvalidAmountInList = false;
                        this.requestAuthorizationSettingDetail();
                        this.$dialog.onOpen({
                            dialogMode: 'Success',
                            content: this.$t("message.saved_successfully"),
                            callback: () => {
                                modalController.dismiss();
                            }
                        })
                    }
                })
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'edit':
                    this.isEdit = false
                    break;
                case 'cancel':
                    this.$dialog.onOpen({
                        dialogMode: 'Question',
                        content: this.$t("message.are_you_sure_you_want_to_cancel_without_saving_data"),
                        callback: () => {
                            modalController.dismiss();
                            this.isEdit = true;
                            this.onResetValueWhenUserChangingAction("cancelAction", false);
                            this.invalidFinal = false;
                        }
                    })
                    break;
                case 'list':
                    this.$router.push({
                        path: "APM204001SC01",
                        query: { customerNo: this.$route.query.customerNo, masterUserID: this.$route.query.masterUserID }
                    })
                    break;
                case 'save':
                    if (this.isNotExistingFinalAuthorizer) {
                        this.invalidFinal = true;
                    } else {
                        this.invalidFinal = false;
                        this.onResetValueWhenUserChangingAction("saveAction", false);
                        var { authStepList } = this.companyAuthorizationLineSettingInfoDetail;
                        var isInvalidAmountValue = false;
                        authStepList.find((item: APM204001sc02_List) => {
                            if (this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN == 'Y') {
                                if (Number(item.minAmount) > Number(item.maxAmount)) {
                                    isInvalidAmountValue = true;
                                }
                            }
                        });
                        var isAmountRageIsY;
                        if (authStepList.length > 0) {
                            isAmountRageIsY = authStepList.find((item) => {
                                if (this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN == 'Y') {
                                    if (item.authorizationRoleCode != 'FINAL' && item.authorizationRoleCode != 'CHECK') {
                                        if (item.minAmount == 0 && item.maxAmount == 0) {
                                            return true;
                                        } else {
                                            return false;
                                        }
                                    }

                                }

                            })
                        }
                        if (isAmountRageIsY) {
                            this.requireField = true;
                        } else {
                            this.requireField = false;
                            if (isInvalidAmountValue) {
                                this.isInvalidAmountInList = true;
                            } else if (this.checkMaxAmountOrder()) {
                                this.isInvalidMaxAmount = true;
                            } else {
                                this.$dialog.onOpen({
                                    dialogMode: 'Question',
                                    content: this.$t("message.would_you_like_to_save"),
                                    callback: async () => {
                                        modalController.dismiss();
                                        this.requestAuthorizationCodetEdit();
                                    }
                                });
                                this.isInvalidMaxAmount = false;
                            }
                        }
                    }
                    break;
            }
        },

        onResetValueWhenUserChangingAction(
            actionType: "reOrder" | "addStep" | "deletStep" | "renderRoleList" | "saveAction" | "cancelAction",
            isChangeAction: boolean
        ) {
            switch (actionType) {
                case 'reOrder':
                    this.isInvalidAmount = isChangeAction;
                    this.isInvalidAmountInList = isChangeAction;
                    this.isRequiredStepField = isChangeAction;
                    break;
                case 'addStep':
                    this.isInvalidAmountInList = isChangeAction;
                    this.isRequiredStepField = isChangeAction;
                    break;
                case 'renderRoleList':
                    this.isInvalidAmount = isChangeAction;
                    this.isInvalidAmountInList = isChangeAction;
                    this.isRequiredStepField = isChangeAction;
                    this.authorizationStep.approverID = "";
                    this.authorizationStep.approverName = "";
                    break;
                case 'saveAction':
                    this.isInvalidAmount = isChangeAction;
                    this.isRequiredStepField = isChangeAction;
                    this.authorizationStep.approverID = "";
                    this.authorizationStep.approverName = "";
                    this.authorizationStep.authorizationRoleCode = "";
                    this.authorizationStep.maxAmount = 0;
                    this.authorizationStep.minAmount = 0;
                    break;
                case 'deletStep':
                    this.isInvalidAmount = isChangeAction;
                    this.isRequiredStepField = isChangeAction;
                    this.isInvalidAmountInList = isChangeAction;
                    break;
                case 'cancelAction':
                    this.isInvalidAmount = isChangeAction;
                    this.isRequiredStepField = isChangeAction;
                    this.isInvalidAmountInList = isChangeAction;
                    this.authorizationStep.approverID = "";
                    this.authorizationStep.approverName = "";
                    this.authorizationStep.authorizationRoleCode = "";
                    this.authorizationStep.maxAmount = 0;
                    this.authorizationStep.minAmount = 0;
                    this.requestAuthorizationSettingDetail();
                    break;
            }

        },
        checkMaxAmountOrder() {
            if (this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN == 'Y') {
                var { authStepList } = this.companyAuthorizationLineSettingInfoDetail;
                for (let i = 1; i < authStepList.length; i++) {
                    if (authStepList[i].authorizationRoleCode != 'FINAL' && authStepList[i].authorizationRoleCode != 'STP' && authStepList[i].authorizationRoleCode != 'CHECK') {
                        if (authStepList[i].maxAmount <= authStepList[i - 1].maxAmount) {
                            return true;
                        }
                    }
                }
            } else {
                return null;
            }
        },
        disabledFinalAuthorizerRangeAmount() {
            if (this.authorizationStep.authorizationRoleCode == 'FINAL' || this.authorizationStep.authorizationRoleCode == 'CHECK' || this.authorizationStep.authorizationRoleCode == 'STP') {
                this.companyAuthorizationLineSettingInfoDetail.requiredAmoungRangeYN = 'N';
                return true
            } else {
                return false
            }
        },
        isOptionDisabled(option) {
            if (option.userRoleID != 'CHECK' && option.userRoleID != 'AUTH' && option.userRoleID != "FINAL" && option.userRoleID != "STP") {
                return true;
            }
        },
        isDisabledFinalAuthorizerSelectOption(option): any {
            const isExist = this.companyAuthorizationLineSettingInfoDetail.authStepList.find(item => item.authorizationRoleCode == 'FINAL');
            if (isExist) {
                if (option.userRoleID == 'FINAL') {
                    return true;
                }
            }
        },
        approvalActiveMode(item, index) {
            console.log('this.previousAuthStep', this.previousAuthStep);
            const previousDataAuthorizationRoleCodeOrder = this.previousAuthStep.map(data => data.authorizationRoleCode);
            const previousDataAuthorizationMinAmount = this.previousAuthStep.map(data => data.minAmount);
            const previousDataAuthorizationMaxAmount = this.previousAuthStep.map(data => data.maxAmount);
            const previousDataAuthorizer = this.previousAuthStep.map(data => data.userName);
            if (previousDataAuthorizationRoleCodeOrder.indexOf(item.authorizationRoleCode) !== index || !previousDataAuthorizationMinAmount.includes(item.minAmount) || !previousDataAuthorizationMaxAmount.includes(item.maxAmount) || !previousDataAuthorizer.includes(item.userName)) {
                return true;
            }
        },
    }
})
